@import url(https://fonts.googleapis.com/css?family=Heebo:400,500,700|Noto+Sans+KR:400,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.app-container{
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.display-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.search-bar {
  display: flex;
  width: 100%;
  height: 100px;
  align-items: center;
  justify-content: center;
}

.movie-selector {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.movie-search {
  width: 250px;
  height: 25px;
  border: 1px solid dodgerblue;
  border-radius: 8px;
  font-size: 20px;
  padding-left: 5px;
  margin-right: 8px;
  box-shadow: 1px 1px 5px #909090;
}

.sub-button {
  color: white;
  background-color: dodgerblue;
  height: 30px;
  border: 1px solid dodgerblue;
  width: 100px;
  border-radius: 8px;
  box-shadow: 1px 1px 5px #909090;
}

.sub-button:hover {
  cursor: pointer;
}

/*6, 7, 8*/
@media only screen and (min-device-width: 375px) and (max-device-height: 667px) {
  .movie-selector {
    justify-content: space-between;
    align-items: center;
    display: flex;
    width: 300px;
  }
}

/*5, SE, and GalaxyS5*/
@media only screen and (min-device-width: 320px) and (max-device-height: 640px) {
  .search-bar {
    display: block;
    height: 50px;
  }

  .movie-selector {
    justify-content: space-between;
    align-items: center;
    display: flex;
    width: 200px;
  }
}


.movie {
  width: 200px;
  border-radius: 4px;
  box-shadow: 1px 1px 5px #909090;
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
}

.movie:hover {
  cursor: pointer;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.movie img {
  width: 100%;
  height: 300px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}


.movie-info {
  padding: 0 8px;
  min-height: 80px;
}

.movie-info p {
  font-family: 'Heebo', sans-serif;
  font-weight: 500;
  margin: 6px 0;
}

/* iphone XS Max */
@media only screen and (min-device-width : 411px) and (max-device-height : 896px)  {
  .movie {
    margin-bottom: 20px;
    width: 250px;
    object-fit: contain;
  }

  .movie:hover {
    cursor: pointer;
    -webkit-transform: none;
            transform: none;
  }
}

