.poster img {
  height: 300px;
  width: 180px;
  margin-right: 10px;
  border-radius: 4px;
}

.selected-movie{
  display: flex;
  justify-content: space-between;
}

.selected-movie p {
  flex-direction: column;
}

.one-title {
  font-family: 'Heebo', sans-serif;
  font-weight: 500;
  font-size: 20px;
  margin-top: 0px;
}

.actors {
  font-family: 'Noto Sans KR'
}

/* iphone XS Max */
@media only screen and (min-device-width : 411px) and (max-device-height : 896px)  {
  .selected-movie {
    margin-bottom: 40px;
    box-shadow: 1px 1px 8px -5px rgba(0, 0, 0, 0.6);
    border-radius: 2px;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  .poster {
    width: 100%;
    margin-bottom: 10px;
  }

  .poster img {
    height: 100%;
    width: 100%;
    object-fit: contain;
    border-radius: 0;
  }

  .selected-movie p {
    padding: 0 15px;
  }
}

